.socials {
  display: flex;
  flex-direction: column;
  min-height: 140px;
  justify-content: space-between;

  @media (max-width: $vp-1024) {
    flex-direction: row;
    width: 100%;
    min-height: auto;
  }

  @media (max-width: $vp-768) {
    flex-wrap: wrap;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    min-height: 120px;
  }
}

.socials__list {
  margin: 0;
  padding: 0;
  list-style: none;
  align-self: flex-start;
  margin-left: 10px;
  width: 150px;
  display: flex;
  flex-wrap: wrap;

  @media (max-width: $vp-1024) {
    margin-right: -4px;
  }

  @media (max-width: $vp-768) {
    align-self: center;
    margin-left: 0;
  }
}

.socials__list li {
  margin-bottom: 10px;
  margin-right: 15px;

  &:nth-child(3n) {
    margin-right: 0;
  }
}

.socials__link {
  transition: all 500ms ease;

  &:focus {
    outline: none;
  }
}

.socials__link:hover svg {
  fill: $color-default-skyblue;
  transition: all 500ms ease;
}

.socials__link:focus svg {
  fill: $color-default-skyblue;
  transition: all 500ms ease;
}

.socials__list svg {
  fill: #f8fdff;
  width: 40px;
  height: 40px;
}
